@charset "UTF-8";
html {
  font-family: sans-serif;
  --ms-text-size-adjust: 100%;
  --webkit-text-size-adjust: 100%; }

body {
  margin: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section,
summary {
  display: block; }

a {
  background: transparent; }
  a:active, a:hover {
    outline: 0; }

b,
strong {
  font-weight: bold; }

img {
  border: 0; }

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0; }

button {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html input[type="button"] {
  --webkit-appearance: button;
  cursor: pointer; }

input {
  line-height: normal; }
  input[type="reset"], input[type="submit"] {
    --webkit-appearance: button;
    cursor: pointer; }
  input[type="checkbox"], input[type="radio"] {
    box-sizing: border-box;
    padding: 0; }
  input[type="number"]::-webkit-inner-spin-button, input[type="number"]::-webkit-outer-spin-button {
    height: auto; }
  input[type="search"] {
    --webkit-appearance: textfield;
    --moz-box-sizing: content-box;
    --webkit-box-sizing: content-box;
    box-sizing: content-box; }
    input[type="search"]::-webkit-search-cancel-button, input[type="search"]::-webkit-search-decoration {
      --webkit-appearance: none; }

legend {
  border: 0;
  padding: 0; }

textarea {
  overflow: auto; }

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  min-width: 320px; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

@font-face {
  font-weight: normal;
  font-family: "Roboto Condensed";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff"); }

@font-face {
  font-weight: bold;
  font-family: "Roboto Condensed";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff"); }

@font-face {
  font-weight: normal;
  font-family: "PT Mono";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff"); }

.page-header {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  flex-direction: column;
  background-image: url("../../img/board.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-color: #1b2d37;
  color: #ffffff; }

.page-header__wrapper {
  width: 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  margin-top: 10px;
  padding-bottom: 50px; }

.page-header__contacts {
  max-width: 700px;
  display: flex;
  align-items: center;
  margin-right: 40px; }

.page-header_logo {
  display: flex;
  align-items: center;
  margin-top: 7px; }

@media (max-width: 1024px) {
  .page-header__wrapper {
    max-width: 768px;
    margin-top: 10px;
    padding-bottom: 50px; }
  .page-header__contacts {
    width: 120px; }
  .page-header_logo {
    margin-top: 3px; } }

@media (max-width: 768px) {
  .page-header {
    width: 100%;
    background-image: url("../../img/board-mobile.jpg");
    background-size: cover; }
  .page-header__wrapper {
    width: 320px; }
  .page-header__contacts {
    width: 80px; }
  .page-header__logo-image {
    width: 38px;
    height: 38px; } }

.list-contacts {
  width: 400px;
  display: flex;
  justify-content: space-between;
  list-style: none;
  line-height: 18px;
  padding: 0;
  font-family: "PT Mono", "Arial", monospace;
  font-size: 16px; }

.list-contacts__mail {
  position: relative; }
  .list-contacts__mail::before {
    position: absolute;
    content: url("../../img/letter.svg");
    top: 18px;
    left: -40px; }

.list-contacts__tel {
  position: relative; }
  .list-contacts__tel::before {
    position: absolute;
    content: url("../../img/phone-call.svg");
    top: 18px;
    left: -40px; }

@media (max-width: 1024px) {
  .list-contacts {
    width: 300px; }
  .list-contacts__item {
    display: none; }
  .list-contacts__mail::before {
    content: url("../../img/letter1.svg");
    top: -15px;
    right: 50px; }
  .list-contacts__tel::before {
    content: url("../../img/phone-call1.svg");
    top: -18px;
    right: 10px; } }

